html, body {
  overflow-x: hidden;
  font-size: 11px;
}

.invoice {
  max-width: 210mm;
  min-height: 210mm;
  margin: 0 auto;
  background-color: rgb(250, 250, 250);
}

p {
  margin: 0;
  font-family: Noto Sans KR;
  font-size: .8rem;
  color: rgb(20, 20, 20, 70%);
  font-weight: 500;
}


.logo {
  padding: 15px;
  flex-direction: column;
  display: flex;
  align-items: center;
  margin-bottom: 0%;
}

.receiver {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer {
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* align-self: flex-end; */
  
  position: absolute;
  left: 0; right: 0; bottom: 0;
  background-color: rgb(250, 250, 250);
  max-width: 210mm;
}

.table-div {
  padding: 15px;
}

table {
  margin-top: 15vh;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  padding: 50px;
}

thead {
  font-family: 'Avenir Next Condensed Medium';
  color: lightgrey;
  border-bottom: 1px lightgrey solid;
}

thead th:nth-child(1) {
  width: 40%;
  text-align: left;
}

thead th:nth-child(2) {
  width: 10%;
  text-align: center;
}

thead th:nth-child(3) {
  width: 10%;
  text-align: center;
}

thead th:nth-child(4) {
  width: 10%;
  text-align: center;
}

tbody td:nth-child(1) {
  width: 40%;
  text-align: left;
  
}

td {
  /* font-family: 'Apple LiGothic'; */
  text-align: center;
  font-family: 'Avenir Next Condensed';
  color: rgb(20, 20, 20, 70%);
}























